.block-editor-page .wpaas-help-support {
  align-items: center;
  background-color: #fff;
  bottom: 0;
  display: flex;
  height: calc(100vh - 92px);
  max-width: 400px;
  padding: 20px;
  padding-top: 82px;
  position: fixed;
  right: -100%;
  transition: right 0.5s cubic-bezier(0.77, 0, 0.175, 1), box-shadow 0.5s cubic-bezier(0.77, 0, 0.175, 1);
  width: 100%;
  z-index: 999;
}
.block-editor-page .wpaas-help-support.visible {
  box-shadow: rgba(0, 0, 0, 0.1) 0 6px 24px 0, rgba(0, 0, 0, 0.01) 0 0 0 1px;
  right: 0;
}
.block-editor-page .wpaas-help-support .close {
  background-color: #1e1e1e;
  border-radius: 2px;
  color: #fff;
  cursor: pointer;
  font-size: 28px;
  font-weight: 400;
  height: 34px;
  left: 20px;
  line-height: 32px;
  opacity: 1;
  position: absolute;
  top: 26px;
  width: calc(100% - 40px);
}
.block-editor-page .wpaas-help-support .close:hover {
  background: var(--wp-admin-theme-color-darker-10);
  opacity: 1;
  transform: scale(1);
}

.logo-menu__logo.has-icon {
  align-items: center;
  align-self: stretch;
  background: #fff;
  border: none;
  border-radius: 0;
  color: #000;
  display: flex;
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
  font-size: 17px;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-style: normal;
  font-weight: 600;
  height: 60px;
  letter-spacing: -0.015em;
  padding: 0 7px 0 17px !important;
  position: relative;
  transition: 200ms background-color linear;
}
.logo-menu__logo.has-icon:hover svg {
  color: var(--wp-admin-theme-color);
}
.logo-menu__logo.has-icon:active {
  color: #000;
}
.logo-menu__logo.has-icon:focus {
  box-shadow: inset 0 0 0 2px #fff, inset 0 0 0 3.5px var(--wp-admin-theme-color) !important;
}
.logo-menu__logo.has-icon svg {
  margin-right: 4px !important;
  width: 36px;
}
.logo-menu__logo.has-icon svg.chevron {
  margin-left: 1px;
  margin-right: 0 !important;
  transition: 100ms transform linear;
  width: 22px;
}
.logo-menu__logo.has-icon svg.chevron.is-open {
  transform: rotate(-180deg);
}

.logo-menu__logo-wrapper {
  align-items: center;
  align-self: stretch;
  display: flex;
  height: 100%;
}

.logo-menu__logo__text {
  display: none;
}
@media (min-width: 480px) {
  .logo-menu__logo__text {
    display: inline;
  }
}
@media (min-width: 782px) {
  .logo-menu__logo__text {
    display: none;
  }
}
@media (min-width: 960px) {
  .logo-menu__logo__text {
    display: inline;
  }
}

.logo-menu__popover .components-popover__content {
  border-color: #1e1e1e;
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
  margin-left: -80px !important;
  margin-top: -1px;
  min-width: 260px !important;
}
.logo-menu__popover .components-menu-group__label {
  margin-bottom: 7px;
  margin-top: 8px;
  padding: 0 10px;
}
.logo-menu__popover .components-menu-group {
  padding: 12px 10px;
}
.logo-menu__popover .components-menu-group + .components-menu-group {
  margin-top: 0;
}
.logo-menu__popover .components-menu-group + .components-menu-group .components-menu-group__label {
  margin-top: 12px;
}
.logo-menu__popover .components-menu-item__button {
  color: #1e1e1e;
  font-size: 14px;
  height: 36px;
  justify-content: space-between !important;
}
.logo-menu__popover .components-menu-item__button:hover {
  color: var(--wp-admin-theme-color);
}
.logo-menu__popover .components-button.has-icon {
  padding: 8px 10px;
}
.logo-menu__popover .components-menu-item__button.wordpress svg {
  width: 21px;
}
.logo-menu__popover .components-menu-item__button svg {
  margin-right: 0 !important;
  order: 2;
}

.edit-post-header__toolbar {
  padding-left: 12px !important;
}

.block-editor-page #gritter-notice-wrapper {
  display: none;
}
.block-editor-page #gritter-notice-wrapper .gritter-item {
  display: inherit;
}